import React from 'react';
import clsx from 'clsx';
import {makeStyles} from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import {Menu, ChevronLeft} from '@material-ui/icons';
import {Divider, List} from "@material-ui/core";
import {socialListItems, marketListItems} from "./Menu";
import ListSubheader from "@material-ui/core/ListSubheader";
import Logo from '../images/logo.png';
import FEGLogo from '../images/Colored.svg';

import {
  HashRouter as Router,
  Route,
  Link as LinkRouter
} from "react-router-dom";

import HomeDashboard from "../dashboards/Home";
import TwitterDashboard from "../dashboards/Twitter";
import TelegramDashboard from "../dashboards/Telegram";
import BinanceDashboard from "../dashboards/Binance";
import EthereumDashboard from "../dashboards/Ethereum";

import '../dashboard.scss';
import ListItemIcon from "@material-ui/core/ListItemIcon";
import HomeIcon from '@material-ui/icons/Home';
import ListItemText from "@material-ui/core/ListItemText";
import ListItem from "@material-ui/core/ListItem";

const routes = [
  {
    "path": "/",
    "component": HomeDashboard,
    "exact": true
  },
  {
    "path": "/twitter",
    "component": TwitterDashboard,
    "exact": true
  },
  {
    "path": "/telegram",
    "component": TelegramDashboard,
    "exact": true
  },
  {
    "path": "/binance",
    "component": BinanceDashboard,
    "exact": true
  },
  {
    "path": "/ethereum",
    "component": EthereumDashboard,
    "exact": true
  }
];

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButtonHidden: {
    display: 'none',
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
    backgroundImage: `url('${FEGLogo}')`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'calc(100% + 140px) bottom',
    backgroundSize: '300px',

  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  title: {
    fontWeight: 700,
    flexGrow: 1,
  },
  titleOpen: {
    display: 'none'
  },
  titleLogo: {
    marginRight: 5
  },
  titleLogoOpen: {
    display: 'none'
  }
}));

export default function Dashboard() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>

      <Router basename="/">
        <CssBaseline/>
        <AppBar className={clsx(classes.appBar, open && classes.appBarShift)}>
          <Toolbar className={classes.toolbar}>
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
            >
              <Menu/>
            </IconButton>
            <img src={Logo} alt="FEGtoken" width={40}
                 className={clsx(classes.titleLogo, open && classes.titleLogoOpen)}/>
            <Typography component="h1" variant="h6" color="inherit" noWrap
                        className={clsx(classes.title, open && classes.titleOpen)}>
              FEGstats
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer
          classes={{
            paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
          }}
          open={open}
          onClick={handleDrawerClose}
        >
          <div className={classes.toolbarIcon}>
            <img src={Logo} alt="FEGtoken" width={40} style={{marginRight: 5}}/>
            <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
              FEGstats
            </Typography>
            <IconButton onClick={handleDrawerClose}>
              <ChevronLeft/>
            </IconButton>
          </div>
          <Divider/>
          <List>
            <ListItem button component={LinkRouter} to="/">
              <ListItemIcon>
                <HomeIcon style={{color: 'white'}} />
              </ListItemIcon>
              <ListItemText primary="Home"/>
            </ListItem>
          </List>
          <Divider/>
          <List
            subheader={
              <ListSubheader component="div">
                Market
              </ListSubheader>
            }>{marketListItems}</List>
          <Divider/>
          <List
            subheader={
              <ListSubheader component="div">
                Social
              </ListSubheader>
            }
          >{socialListItems}</List>
        </Drawer>
        <main className={classes.content}>
          <div className={classes.appBarSpacer}/>
          <Container maxWidth="lg" className={classes.container}>
            <Grid container spacing={2}>
              {routes.map((route) => (
                <Route key={route.path} exact={route.exact} path={route.path} component={route.component}/>
              ))}
            </Grid>
            <Box pt={4}>
              {/*<Copyright/>*/}
            </Box>
          </Container>
        </main>
      </Router>
    </div>
  );
}
