import axios from "axios";

class PriceData {

  constructor() {
    this.dataSets = {};
    this.data = undefined;
  }

  setTimeframe = (window) => {
    this.data = this.dataSets[window];
  }

  fetchData = () => {

    const request1h = axios.get('https://d2utyjas1q2mfz.cloudfront.net/token_price/1h.json')
    const request24h = axios.get('https://d2utyjas1q2mfz.cloudfront.net/token_price/24h.json')
    const request7d = axios.get('https://d2utyjas1q2mfz.cloudfront.net/token_price/168h.json')

    return axios
      .all([request1h, request24h, request7d])
      .then(axios.spread((...responses) => {
        this.dataSets['1h'] = this.massageData(responses[0])
        this.dataSets['24h'] = this.massageData(responses[1])
        this.dataSets['7d'] = this.massageData(responses[2])
        this.setTimeframe('1h');
      }))
  }

  massageData = (d) => {

    const {btc_eth_price, feg_price} = d.data;

    const data = {
      btc: {current: null, timeseries: []},
      eth: {current: null, timeseries: []},
      bnb: {current: null, timeseries: []},
      fegbsc: {current: null, timeseries: []},
      fegeth: {current: null, timeseries: []}
    }

    btc_eth_price.buckets.map(p => {
      if(p[1].value !== null) {
        data.btc.timeseries.push({
          x: p.key,
          y: p[1].value
        })
      }

      if(p[3].value !== null) {
        data.eth.timeseries.push({
          x: p.key,
          y: p[3].value
        })
      }

      if(p[4].value !== null) {
        data.bnb.timeseries.push({
          x: p.key,
          y: p[4].value
        })
      }
      return p;
    })

    feg_price.buckets.map((p, i) => {
      if(p[1].value !== null){
        data.fegbsc.timeseries.push({
          x: p.key,
          y: p[1].value
        })
      }

      if(p[3].value !== null) {
        data.fegeth.timeseries.push({
          x: p.key,
          y: p[3].value
        })
      }
      return p;
    });

    //clean off last element, sometimes errors
    data.btc.timeseries.pop();
    data.eth.timeseries.pop();
    data.bnb.timeseries.pop();
    data.fegbsc.timeseries.pop();
    data.fegeth.timeseries.pop();

    data.btc.current = parseFloat(data.btc.timeseries[data.btc.timeseries.length-1].y).toFixed(2);
    data.eth.current = parseFloat(data.eth.timeseries[data.eth.timeseries.length-1].y).toFixed(2);
    data.bnb.current = parseFloat(data.bnb.timeseries[data.bnb.timeseries.length-1].y).toFixed(2);
    data.fegbsc.current = parseFloat(data.fegbsc.timeseries[data.fegbsc.timeseries.length-1].y).toFixed(12);
    data.fegeth.current = parseFloat(data.fegeth.timeseries[data.fegeth.timeseries.length-1].y).toFixed(12);

    return data;

  }

}

export default new PriceData();
