import * as React from "react";
import {ArcSeries, XYPlot} from "react-vis";
import {Paper, Typography} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {Autorenew, Whatshot} from "@material-ui/icons";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles((theme) => ({
  xyplot: {
    margin: '0 auto'
  }
}))

function BurnSupply(props) {

  const classes = useStyles();

  const {burnt, supply, percent} = props;

  const angle = (360 * percent) * (Math.PI / 180);

  return (<>
    <XYPlot
      xDomain={[-5, 5]}
      yDomain={[-5, 5]}
      width={300}
      height={300}
      style={{
        transform: 'translate(-15px, 5px)'
      }}
      className={classes.xyplot}
    >
      <ArcSeries
        radiusDomain={[0, 10]}
        center={{x: 0, y: 0}}
        data={[
          {angle0: 0, angle: Math.PI * 2, opacity: 1, radius: 7.5, radius0: 7, color: "#FF003E"},
          {angle0: 0, angle: angle, opacity: 1, radius: 8.5, radius0: 6, color: "#FF7B00"}
        ]}
        colorType={'literal'}
      />
    </XYPlot>
    <Typography style={{
      color:"white",
      height: 0,
      width: '100%',
      textAlign: 'center',
      top: -225,
      position: 'relative',
      fontSize: 70,
      fontWeight: '700'
    }}>{(percent * 100).toFixed(0)}<span style={{fontSize:14,fontWeight: '400',marginTop:-20, display: 'block'}}>% BURNT</span></Typography>
    <Paper style={{padding:20, textAlign: 'center',marginTop:-20}}>
      <Grid container direction="row" alignItems="center">
        <Grid item>
          <Whatshot style={{color:"#FF7B00", marginRight: 10, marginTop: 3}} />
        </Grid>
        <Grid item>
          <Typography style={{color:"#FF7B00"}}>{burnt.toLocaleString()} <Typography variant="caption">BURNT</Typography></Typography>
        </Grid>
      </Grid>
      <Grid container direction="row" alignItems="center">
        <Grid item>
          <Autorenew style={{color:"#FF003E", marginRight: 10, marginTop: 5}} />
        </Grid>
        <Grid item>
          <Typography style={{color:"#FF003E"}}>{supply.toLocaleString()} <Typography variant="caption">SUPPLY</Typography></Typography>
        </Grid>
      </Grid>
    </Paper>
  </>)
}

export default BurnSupply
