import * as React from "react";
import {Button, Grid, Hidden, IconButton, Link, Typography} from "@material-ui/core";
import TimeseriesWidget from "../components/TimeseriesWidget";
import InfluencerList from "../components/InfluencerList";
import {Twitter} from "@material-ui/icons";
import TwitterData from "../data/TwitterData";
import StatWidget from "../components/StatWidget";

class TwitterDashboard extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      timeframe: '24h'
    }
  }

  componentDidMount() {
    TwitterData.fetchData().then(() => {
      this.setState({
        data: TwitterData.data
      })
    })
  }

  updateTimeframe = (t) => {
    TwitterData.setTimeframe(t);
    this.setState({
      timeframe: t,
      data: TwitterData.data
    })
  }

  render = () => {
    return <>
      <Grid item xs={12}>
        <Typography variant='h5'>Twitter Dashboard
          <Hidden xsDown>
            <Button
              variant="contained"
              style={{color: 'white',backgroundColor:"#00ACEE", float: "right"}}
              component={Link}
              href='https://twitter.com/intent/follow?screen_name=FEGtoken'
              target="_blank"
            >
              <Twitter /> @FEGtoken
            </Button>
          </Hidden>
        </Typography>
      </Grid>
      <Grid item xs={12} md={9}>
        <TimeseriesWidget
          timeframe={this.state.timeframe}
          update={this.updateTimeframe}
          count={this.state.data?.tweets}
          timeseries={this.state.data?.timeseries}
          timeframeOptions={['24h', '7d', '30d']}
          countLabel="TWEETS"
        />
      </Grid>
      <Grid item xs={12} md={3}>
        <StatWidget
          label='Users'
          count={this.state.data?.users} />
        <StatWidget
          label='Mentions'
          count={this.state.data?.mentions}
          PaperProps={{style:{marginTop: 16}}} />
      </Grid>
      <Grid item xs={12} md={4}>
        <InfluencerList
          list={this.state.data?.influencers.favorites}
          title="Influencers With Most Favorites"
          countLabel="Favorites"
        />
      </Grid>
      <Grid item xs={12} md={4}>
        <InfluencerList
          list={this.state.data?.influencers.retweets}
          title="Influencers With Most Retweets"
          countLabel="Retweets"
        />
      </Grid>
      <Grid item xs={12} md={4}>
        <InfluencerList
          list={this.state.data?.influencers.quoted}
          title="Influencers With Quoted Tweets"
          countLabel="Quoted Tweets"
        />
      </Grid>
      <Hidden smUp>
        <IconButton
          variant="contained"
          style={{
            color: 'white',
            backgroundColor:"#00ACEE",
            position: 'fixed',
            bottom: 10,
            right: 10
          }}
          component={Link}
          href='https://twitter.com/intent/follow?screen_name=FEGtoken'
          traget="_blank"
        >
          <Twitter />
        </IconButton>
      </Hidden>
    </>;
  }
}

export default TwitterDashboard;
