import * as React from "react";
import {CircularProgress, Grid} from "@material-ui/core";
import PriceData from "../data/PriceData";
import TokenInfoWidget from "../components/TokenInfoWidget";

class HomeDashboard extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      timeframe: '1h',
      price_data: null,
    }
  }

  componentDidMount() {
    PriceData.setTimeframe('1h');
    PriceData.fetchData().then(() => {
      this.setState({
        price_data: PriceData.data
      })
    })
  }

  render = () => {

    if (!this.state.price_data) {
      return <Grid item xs={12} style={{textAlign: 'center', marginTop: 40}}>
        <CircularProgress color="secondary"/>

      </Grid>
    }

    return <>
        <TokenInfoWidget
          tokenName="FEG Token"
          tokenSymbol="FEGbsc"
          price={this.state.price_data.fegbsc.current}
          timeseries={this.state.price_data.fegbsc.timeseries}
        />

        <TokenInfoWidget
          tokenName="FEG Token"
          tokenSymbol="FEGeth"
          price={this.state.price_data.fegeth.current}
          timeseries={this.state.price_data.fegeth.timeseries}
        />

        <TokenInfoWidget
          tokenName="Bitcoin"
          tokenSymbol="BTC"
          price={this.state.price_data.btc.current}
          timeseries={this.state.price_data.btc.timeseries}
        />

        <TokenInfoWidget
          tokenName="Ethereum"
          tokenSymbol="ETH"
          price={this.state.price_data.eth.current}
          timeseries={this.state.price_data.eth.timeseries}
        />

        <TokenInfoWidget
          tokenName="Binance"
          tokenSymbol="BNB"
          price={this.state.price_data.bnb.current}
          timeseries={this.state.price_data.bnb.timeseries}
        />
    </>
  }

}

export default HomeDashboard;
