import axios from "axios";

class TwitterData {

  constructor() {
    this.dataSets = {};
    this.data = undefined;
  }

  //timeframe adjustable
  setTimeframe = (window) => {
    this.data = this.dataSets[window];
  }

  fetchData = () => {

    const request24h = axios.get('https://d2utyjas1q2mfz.cloudfront.net/twitter/24h.json')
    const request7d = axios.get('https://d2utyjas1q2mfz.cloudfront.net/twitter/168h.json')
    const request30d = axios.get('https://d2utyjas1q2mfz.cloudfront.net/twitter/720h.json')

    return axios
        .all([request24h, request7d, request30d])
        .then(axios.spread((...responses) => {
          this.dataSets['24h'] = this.massageData(responses[0])
          this.dataSets['7d'] = this.massageData(responses[1])
          this.dataSets['30d'] = this.massageData(responses[2])
          this.setTimeframe('24h');
        }))
  }

  //cache influencer avatars
  getImage = (handle) => {

    const avatarKey = "twitter_avatar_" + handle

    let avatar = JSON.parse(localStorage.getItem(avatarKey));

    let recent = false;

    //check if avatar is within last 7 days
    if(avatar) recent = (7 * 24 * 60 * 60 * 1000) > (new Date().getTime() - new Date(avatar.updated).getTime());

    //fetch and store avatar for next time, or update
    if(!avatar || !recent) {
      let image = new Image();
      image.crossOrigin="anonymous";
      image.onload = function() {
        let canvas = document.createElement('CANVAS');
        let ctx = canvas.getContext('2d');
        let dataURL;
        canvas.height = 40;
        canvas.width = 40;
        ctx.drawImage(this, 0, 0, this.width, this.height, 0, 0 , 40, 40);
        dataURL = canvas.toDataURL();

        //store to local storage
        localStorage.setItem(avatarKey, JSON.stringify({
          updated: new Date(),
          src: dataURL
        }));
      };
      image.src = 'https://unavatar.vercel.app/twitter/' + handle;
      return image.src;
    }

    //return saved avatar
    return avatar.src;
  }

  massageData = (d) => {

    const {data} = d;

    const nData = {
      tweets: data.tweet_count,
      timeseries: [],
      users: data.tweet_user_count,
      mentions: data.tweet_mentions,
      influencers: {
        favorites: [],
        retweets: [],
        quoted: []
      }
    };

    //tweet_count_timeseries
    nData.timeseries = data.tweet_count_timeseries.timeseries.buckets.map(t => {
      return {
        x: t.key,
        y: t.doc_count
      }
    });
    //remove last element, as it usually drags the end of chart down
    nData.timeseries.pop();

    //influencer_favorites
    nData.influencers.favorites = data.influencer_favorites.favorites.buckets.map(i => {
      return {
        username: i.key,
        count: i.favorites.value,
        image: this.getImage(i.key)
      }
    });

    //influencer_retweets
    nData.influencers.retweets = data.influencer_retweets.influencer.buckets.map(i => {
      return {
        username: i.key,
        count: i.retweets.value,
        image: this.getImage(i.key)
      }
    });

    //influencer_quoted_tweets
    nData.influencers.quoted = data.influencer_quoted_tweets.quoted.buckets.map(i => {
      return {
        username: i.key,
        count: i.quoted.value,
        image: this.getImage(i.key)
      }
    });

    return nData;
  }
}

export default new TwitterData();
