import {Link, List, ListItem, ListItemText, Paper, Typography} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: 20
  },
  text: {
    fontSize: 14,
    textTransform: 'uppercase'
  }
}))

function TransactionList(props) {

  const classes = useStyles();

  const {data, link, title} = props;

  return (
    <Paper className={classes.paper}>
      <Typography className={classes.text} variant='caption'>{title}</Typography>
      <List>
        {
          data.map(t => {
            return <ListItem
              disableGutters
              key={t.wallet}
              button
              component={Link}
              href={`${link}${t.wallet}`}
              target="_blank"
            >
              <ListItemText>
                <Typography variant='caption'
                            style={{color: "white"}}>{t.wallet.substr(0, 6)}...{t.wallet.substr(-6)}</Typography>
                <Typography variant='caption'
                            style={{float: 'right'}}>{parseInt(t.value.toFixed(0)).toLocaleString()}</Typography>
              </ListItemText>
            </ListItem>
          })
        }

      </List>
    </Paper>
  )
}

export default TransactionList;
