import Web3 from 'web3';
import tokens from "../utils/tokenConfig";

class TokenData {

  constructor() {

    const ethWeb3 = new Web3("https://mainnet.infura.io/v3/36e71e4fb7e649d6a4b8e3b0ac363a92");
    const bscWeb3 = new Web3("https://bsc-dataseed.binance.org/");

    this.contracts = {
      fegbsc: new bscWeb3.eth.Contract(tokens.fegbsc.abi, tokens.fegbsc.address),
      fegeth: new ethWeb3.eth.Contract(tokens.fegeth.abi, tokens.fegeth.address)
    }

    this.fegbsc = {};
    this.fegeth = {};
    this.decimals = 9;
    this.totalSupply = 100000000000000000000000000;
  }

  fetchData = () => {
    return new Promise((resolve, reject) => {
      Promise.all([
        this._getBSCSupply(),
        // this._getBSCRewards(),
        this._getETHSupply(),
        // this._getETHRewards()
      ])
        .then((d) => {
          resolve(d)
        })
    });
  }

  _getETHSupply = () => {
    return new Promise((resolve, reject) => {
      this
        .contracts
        .fegeth
        .methods
        .balanceOf(tokens.fegeth.burnAddress)
        .call()
        .then(bal => {
          const supply = this.totalSupply - bal;

          this.fegeth.supply = supply / Math.pow(10, this.decimals);
          this.fegeth.burnt = bal / Math.pow(10, this.decimals);
          this.fegeth.percent = parseFloat(bal / this.totalSupply);
          resolve(true);
        });
    })
  }

  _getETHRewards = () => {
    return new Promise((resolve, reject) => {
      this
        .contracts
        .fegeth
        .methods
        .totalFees()
        .call()
        .then(rewards => {
          this.fegeth.rewards = (rewards / Math.pow(10, this.decimals));
          resolve(true);
        });
    });
  }

  _getBSCSupply = () => {
    return new Promise((resolve, reject) => {
      this
        .contracts
        .fegbsc
        .methods
        .balanceOf(tokens.fegbsc.burnAddress)
        .call()
        .then(bal => {
          const supply = this.totalSupply - bal;

          this.fegbsc.supply = supply / Math.pow(10, this.decimals);
          this.fegbsc.burnt = bal / Math.pow(10, this.decimals);
          this.fegbsc.percent = parseFloat(bal / this.totalSupply);
          resolve(true);
        });
    });
  }

  _getBSCRewards = () => {
    return new Promise((resolve, reject) => {
      this
        .contracts
        .fegbsc
        .methods
        .totalFees()
        .call()
        .then(rewards => {
          this.fegbsc.rewards = (rewards / Math.pow(10, this.decimals));
          resolve(true);
        });
    });
  }
}

export default new TokenData();
