import {Paper, Typography} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {Skeleton} from "@material-ui/lab";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: 20
  },
  text: {
    fontSize: 14,
    textTransform: 'uppercase'
  }
}))

function StatWidget(props) {

  const classes = useStyles();

  const {count, label, PaperProps} = props;

  let content;
  if (!count) {
    content = <Skeleton variant="rect" height={56}/>
  } else {
    content = <Typography variant={parseInt(count) < 1000000000 ? 'h3' : 'h5'}>{count.toLocaleString()}</Typography>
  }

  return (
    <Paper className={classes.paper} {...PaperProps}>
      {content}
      <Typography className={classes.text} variant='caption'>{label}</Typography>
    </Paper>
  )
}

export default StatWidget;
