import axios from "axios";
import {getColor} from '../utils';

class TransactionData {

  constructor() {
    this.dataSets = {};
    this.data = undefined;
  }

  setTimeframe = (window) => {
    this.data = this.dataSets[window];
  }

  fetchData = () => {

    const request1h = axios.get('https://d2utyjas1q2mfz.cloudfront.net/feg_transaction_data/1h.json')
    const request24h = axios.get('https://d2utyjas1q2mfz.cloudfront.net/feg_transaction_data/24h.json')
    const request7d = axios.get('https://d2utyjas1q2mfz.cloudfront.net/feg_transaction_data/168h.json')

    return axios
      .all([request1h, request24h, request7d])
      .then(axios.spread((...responses) => {
        this.dataSets['1h'] = this.massageData(responses[0])
        this.dataSets['24h'] = this.massageData(responses[1])
        this.dataSets['7d'] = this.massageData(responses[2])
        this.setTimeframe('1h');
      }))
  }

  massageData = (d) => {

    const {
      bsc_top_ten_buy,
      bsc_top_ten_sell,
      bsc_trading_volume_metric,
      bsc_trading_volume_time_series,
      eth_top_ten_buy,
      eth_top_ten_sell,
      eth_trading_volume_metric,
      eth_trading_volume_time_series
    } = d.data;

    const data = {
      fegbsc: {
        ratio: {},
        buys: [],
        sells: [],
        volume: {
          buy: [],
          sell: [],
          transfer: []
        }
      },
      fegeth: {
        ratio: {},
        buys: [],
        sells: [],
        volume: {
          buy: [],
          sell: [],
          transfer: []
        }
      }
    }

    let total = 0;
    bsc_trading_volume_metric.buckets.map(t => {
      total+=t[1].value;
      return t;
    })

    bsc_trading_volume_metric.buckets.map(t => {
      data.fegbsc.ratio[t.key] = {
        value: parseInt(t[1].value.toFixed(0)),
        color: getColor(t.key),
        percent: t[1].value/total
      }
      return t;
    })

    bsc_top_ten_buy.buckets.map(buy => {
      data.fegbsc.buys.push({
        wallet: buy.key,
        value: buy[1].value
      })
      return buy;
    })

    bsc_top_ten_sell.buckets.map(sell => {
      data.fegbsc.sells.push({
        wallet: sell.key,
        value: sell[1].value
      })
      return sell;
    })

    bsc_trading_volume_time_series.buckets.map(t => {
      t[3].buckets.map(t2 => {
        data.fegbsc.volume[t2.key].push({
          x: t.key,
          y: t2[1].value
        })
        return t2;
      })
      return t;
    });

    total = 0;
    eth_trading_volume_metric.buckets.map(t => {
      total+=t[1].value;
      return t;
    })

    eth_trading_volume_metric.buckets.map(t => {
      data.fegeth.ratio[t.key] = {
        value: parseInt(t[1].value.toFixed(0)),
        color: getColor(t.key),
        percent: t[1].value/total
      }
      return t;
    })

    eth_top_ten_buy.buckets.map(buy => {
      data.fegeth.buys.push({
        wallet: buy.key,
        value: buy[1].value
      })
      return buy;
    })

    eth_top_ten_sell.buckets.map(sell => {
      data.fegeth.sells.push({
        wallet: sell.key,
        value: sell[1].value
      })
      return sell;
    })

    eth_trading_volume_time_series.buckets.map(t => {
      t[3].buckets.map(t2 => {
        data.fegeth.volume[t2.key].push({
          x: t.key,
          y: t2[1].value
        })
        return t2;
      })
      return t;
    });

    return data;
  }

}

export default new TransactionData();
