import * as React from "react";
import {Button, CircularProgress, Hidden, Link, Paper, Typography} from "@material-ui/core";
import PriceData from "../data/PriceData";
import TransactionData from "../data/TransactionData";
import {FlexibleXYPlot, VerticalBarSeries} from "react-vis";
import Grid from "@material-ui/core/Grid";
import TransactionList from "../components/TransactionList";
import TimeseriesWidget from "../components/TimeseriesWidget";

import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import SettingsEthernetIcon from '@material-ui/icons/SettingsEthernet';

import {getColor} from '../utils';

import EtherScanLogo from '../images/logo-ether.png';
import BurnSupply from "../components/BurntSupply";
import TokenData from "../data/TokenData";

class EthereumDashboard extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      timeframe: '1h',
      transaction_data: null,
      burnt: 0,
      supply: 0,
      percent: 0,
      rewards: 0
    }
  }

  componentDidMount() {
    PriceData.fetchData().then(() => {
      this.setState({
        price_data: PriceData.data
      })
    });

    TransactionData.fetchData().then(() => {
      this.setState({
        transaction_data: TransactionData.data.fegeth
      })
    });

    TokenData.fetchData().then((x)=> {
      this.setState({
        burnt: TokenData.fegeth.burnt,
        supply: TokenData.fegeth.supply,
        percent: TokenData.fegeth.percent
      })
    })
  }

  updateTimeframe = (t) => {
    PriceData.setTimeframe(t);
    TransactionData.setTimeframe(t);
    this.setState({
      timeframe: t,
      price_data: PriceData.data,
      transaction_data: TransactionData.data.fegeth
    })
  }

  render = () => {

    if (!this.state.transaction_data || !this.state.price_data) {
      return <Grid xs={12} style={{textAlign: 'center', marginTop: 40}}>
        <CircularProgress color="secondary"/>
      </Grid>
    }

    return <>
      <Grid item xs={12}>
        <Typography variant='h5'>Ethereum Dashboard
          <Hidden xsDown>
            <Button
              variant="contained"
              style={{color: 'white',backgroundColor:"white", float: "right"}}
              component={Link}
              href='https://bscscan.com/token/0xacfc95585d80ab62f67a14c566c1b7a49fe91167'
              target="_blank"
            >
              <img src={EtherScanLogo} alt="Ethereum" height={24}/>
            </Button>
          </Hidden>
        </Typography>
      </Grid>

      <Grid item xs={12} md={6}>
        <TimeseriesWidget
          timeframe={this.state.timeframe}
          update={this.updateTimeframe}
          count={this.state.price_data.fegeth.current}
          timeseries={this.state.price_data.fegeth.timeseries}
          timeframeOptions={['1h', '24h', '7d']}
          countLabel="FEGeth PRICE"
          toFixed={12}/>
      </Grid>

      <Grid item xs={12} md={6}>
        <TimeseriesWidget
          timeframe={this.state.timeframe}
          update={this.updateTimeframe}
          count={this.state.price_data.eth.current}
          timeseries={this.state.price_data.eth.timeseries}
          timeframeOptions={['1h', '24h', '7d']}
          countLabel="ETH PRICE"
          toFixed={2}
        />
      </Grid>

      {/*<Grid item xs={12}>*/}
      {/*  <StatWidget*/}
      {/*    count={this.state.rewards}*/}
      {/*    label={`TOTAL FEES`}*/}
      {/*    PaperProps={{*/}
      {/*      style: {color: "#bada55"}*/}
      {/*    }}*/}
      {/*  />*/}
      {/*</Grid>*/}

      <Grid item xs={12}>
        <BurnSupply
          burnt={this.state.burnt || 0}
          supply={this.state.supply || 0}
          percent={this.state.percent || 0}
        />
      </Grid>

      <Grid item xs={12}>
        <Paper style={{padding: 20}}>
          <Typography>TRANSACTION VOLUME</Typography>
          <FlexibleXYPlot
            className="clustered-stacked-bar-chart-example"
            xType="ordinal"
            stackBy="y"
            height={200}
            style={{transform: 'translate(-15px, 15px)'}}
          >
            <VerticalBarSeries
              color={getColor("buy")}
              data={this.state.transaction_data.volume.buy}
            />
            <VerticalBarSeries
              color={getColor("sell")}
              data={this.state.transaction_data.volume.sell}
            />
            <VerticalBarSeries
              color={getColor("transfer")}
              data={this.state.transaction_data.volume.transfer}
            />
          </FlexibleXYPlot>

          <Grid container direction="row" alignItems="center">
            <Grid item>
              <ArrowUpwardIcon style={{color:getColor('buy'), marginRight: 10, marginTop: 3}} />
            </Grid>
            <Grid item>
              <Typography style={{color:getColor('buy')}}>{this.state.transaction_data.ratio.buy.value.toLocaleString() || 0} <Typography variant="caption">BOUGHT</Typography></Typography>
            </Grid>
          </Grid>

          <Grid container direction="row" alignItems="center">
            <Grid item>
              <ArrowDownwardIcon style={{color:getColor('sell'), marginRight: 10, marginTop: 5}} />
            </Grid>
            <Grid item>
              <Typography style={{color:getColor('sell')}}>{this.state.transaction_data.ratio.sell.value.toLocaleString() || 0} <Typography variant="caption">SOLD</Typography></Typography>
            </Grid>
          </Grid>

          <Grid container direction="row" alignItems="center">
            <Grid item>
              <SettingsEthernetIcon style={{color:getColor('transfer'), marginRight: 10, marginTop: 7}} />
            </Grid>
            <Grid item>
              <Typography style={{color:getColor('transfer')}}>{this.state.transaction_data.ratio.transfer.value.toLocaleString() || 0} <Typography variant="caption">TRANSFERRED</Typography></Typography>
            </Grid>
          </Grid>

        </Paper>
      </Grid>

      <Grid item xs={12} md={6}>
        <TransactionList
          title="Top 10 Buyers"
          link='https://etherscan.io/token/0x389999216860ab8e0175387a0c90e5c52522c945?a='
          data={this.state.transaction_data.buys}
        />
      </Grid>
      <Grid item xs={12} md={6}>
        <TransactionList
          title="Top 10 Sellers"
          link='https://etherscan.io/token/0x389999216860ab8e0175387a0c90e5c52522c945?a='
          data={this.state.transaction_data.sells}
        />
      </Grid>
    </>
  }
}

export default EthereumDashboard;
