import React from 'react';
import {Grid, Paper, Typography} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";

import FEGLogo from '../images/colored-original.svg';
import BTCLogo from '../images/btc-logo.png';
import ETHLogo from '../images/ethereum.png';
import BNBLogo from '../images/binance.png';
import {LineSeries, XYPlot} from "react-vis";


const useStyles = makeStyles((theme) => ({
  paper: {
    color: 'white',
    borderRadius: 8
  },
  avatar: {
    padding: 10,
    height: '100%',
    margin: 'auto',
    textAlign: 'center'
  },
  image: {
    width: '60%',
    maxWidth: 32,
    verticalAlign: 'middle'
  },
  content: {
    padding: '10px 0'
  },
  text: {
    fontSize: 14,
    textTransform: 'uppercase'
  },
  xyplot: {
    marginLeft: -30
  }
}))

function TokenInfoWidget(props) {

  const {tokenName, tokenSymbol, price, timeseries} = props;

  const classes = useStyles();

  return (
    <Grid item xs={12} sm={12} md={6} lg={4}>
      <Paper className={classes.paper}>
        <Grid container>
          <Grid item xs={3} className={classes.avatar}>
            <img src={tokenLogo(tokenSymbol)} alt={tokenSymbol} className={classes.image}/>
          </Grid>
          <Grid item xs={6} className={classes.content}>
            <Typography><b>{tokenName}</b> <Typography variant="caption">{tokenSymbol}</Typography></Typography>
            <Typography>{price}</Typography>
          </Grid>
          <Grid item xs={3}>
            <XYPlot height={70} width={120} className={classes.xyplot}>
              <LineSeries
                data={timeseries}
                strokeWidth={2}
                stroke='white'
                curve='natural'
                style={{fill: 'transparent', transform: 'translate(25px, 25px)'}}
              />
            </XYPlot>
          </Grid>
        </Grid>
      </Paper>
    </Grid>)
}

export default TokenInfoWidget;


function tokenLogo(symbol) {
  switch (symbol.toLowerCase()) {
    case "fegbsc":
    case "fegeth":
      return FEGLogo;
    case "btc":
      return BTCLogo;
    case "eth":
      return ETHLogo;
    case "bnb":
      return BNBLogo;
    default:
      break;
  }
}
