import {FormControl, MenuItem, Paper, Select, Typography} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import {Crosshair, FlexibleWidthXYPlot, LineSeries} from "react-vis";
import {Skeleton} from "@material-ui/lab";
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import {useEffect, useState} from "react";
import moment from 'moment';
import withWidth from '@material-ui/core/withWidth';


const useStyles = makeStyles((theme) => ({
  paper: {
    padding: '20px 20px 0 20px',
    color: 'white',
    height: 255
  },
  text: {
    fontSize: 14,
    textTransform: 'uppercase'
  }
}))

function TimeseriesWidget(props) {

  const classes = useStyles();

  const {timeframe, count, timeseries, timeframeOptions, countLabel, color, toFixed} = props;

  const [crosshairValues, setCrosshairValues] = useState([])
  const [nCount, setNCount] = useState(count)
  const [nCountLabel, setNCountLabel] = useState(countLabel)

  useEffect(() => {
    setNCount(parseFloat(count).toLocaleString("en-US", {minimumFractionDigits: toFixed}));
    setNCountLabel(countLabel);
  }, [count, countLabel, toFixed]);

  let content;


  const _onMouseLeave = () => {
    setCrosshairValues([]);
    setNCount(parseFloat(count).toLocaleString("en-US", {minimumFractionDigits: toFixed}));
    setNCountLabel(countLabel);
  };

  const _onNearestX = (value, {index}) => {
    setCrosshairValues([value]);
    setNCount(parseFloat(value.y).toLocaleString("en-US", {minimumFractionDigits: toFixed}));
    setNCountLabel(countLabel + " - " + moment(value.x).format('MMM DD HH:mm'));
  };


  if (!count) {
    content = <Skeleton variant="rect" height={190}/>
  } else {
    content = <>

      <FormControl style={{color: 'white', float: 'right'}} className="timeseries-form-control">
        <Select
          value={timeframe}
          onChange={(ev) => {
            props.update(ev.target.value)
          }}
          SelectDisplayProps={{
            className: "MuiSelect-root MuiSelect-select MuiSelect-selectMenu MuiInputBase-input MuiInput-input timeseries-select",
            style: {
              color: 'white'
            }
          }}
          IconComponent={KeyboardArrowDownIcon}
        >
          {
            timeframeOptions.map(tf => {
              return <MenuItem key={tf} value={tf}>{tf.toUpperCase()}</MenuItem>
            })
          }
        </Select>
      </FormControl>

      <Typography variant={toFixed > 4 && props.width === "xs" ? 'h5' : 'h3'}>{nCount} </Typography>
      <Typography className={classes.text} variant='caption'>
        {nCountLabel}
      </Typography>
      <FlexibleWidthXYPlot onMouseLeave={_onMouseLeave} height={135} className="timeseries-container">
        <LineSeries
          data={timeseries}
          strokeWidth={3}
          stroke='white'
          onNearestX={_onNearestX}
        />
        <Crosshair
          values={crosshairValues}
          className={'timerseries-crosshair'}
          style={{
            line: {
              background: 'white'
            }
          }}
        >
          <div id="timerseries-crosshair-line"/>
        </Crosshair>
      </FlexibleWidthXYPlot>
    </>
  }

  return (
    <Paper className={classes.paper} style={{backgroundColor: color || ''}}>
      {content}
    </Paper>
  )
}

export default withWidth()(TimeseriesWidget);
